
img {
  width: 240px;
  margin-top: 1%;
}

.bar {
  background: #C44D33;
  color: white;
  width: 10rem;
  -ms-flex-line-pack: center;
      align-content: center;
  margin-left: 80%;
  margin-top: -535px;
  padding-left: 0px;
  text-align: center;
  -webkit-transform: rotate(23deg) skew(0,3deg);
      -ms-transform: rotate(23deg) skew(0,3deg);
          transform: rotate(23deg) skew(0,3deg);
  -webkit-box-shadow: 1em 1em 2rem #008b8b;
          box-shadow: 1em 1em 2rem #008b8b;
}

.chunk {
  padding-top: 2.5rem;
  padding-bottom: 2rem;
  padding-left: 0px;
  background: #222;
}

.chunk:hover {
  -webkit-transform: translate(5%, -10%);
      -ms-transform: translate(5%, -10%);
          transform: translate(5%, -10%);
  -webkit-transition: all .3s linear;
  -o-transition: all .3s linear;
  transition: all .3s linear;
  color: #C44D33;
}

a {
  color: white;
}
/* unvisited link */
a:link {
  color: white;
}

/* visited link */
a:visited {
  color: white;
}

/* mouse over link */
a:hover {
  color: #C44D33;
}

/* selected link */
a:active {
  color: white;
}


@media only screen and (max-width: 500px) {
  .bar {
    -webkit-transform: rotate(0deg) skew(0,0deg);
        -ms-transform: rotate(0deg) skew(0,0deg);
            transform: rotate(0deg) skew(0,0deg);
    float: left;
    margin-top: 25px;
    margin-left: 23%;
    width: 15rem;
    -webkit-box-shadow: none;
            box-shadow: none;
  }
}


